<template>
    <section v-for="video in videos" :key="video._id" class="video">
        <div class="presentation">
            <span>Publicado el {{video.date}}</span>
            <h3>{{video.title}}</h3>         
            <img :src="'https://horizontes.herokuapp.com/images/profiles/' + video.authorProfile" :alt="video.authorProfile">
            <p>{{video.author}}</p>            
        </div>        
        <div class="video-container">
            <iframe 
                :src="video.link" title="YouTube video player" frameborder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen>
            </iframe>
        </div>
        <div class="description">
            <p>Descripción:</p>
            <p>
                {{video.description}}
            </p>
        </div>
    </section>
</template>

<script>
export default {
    name: "Video",
    props: {
        videos: Array
    }
}
</script>

<style lang="scss" scoped>
.video {
    box-shadow: 0px 0px 8px 0px #00000014;
    line-height: 1.4;
    margin: 2rem 0;
}
.presentation {
    position: relative;
    width: 560px;
    padding: 1rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    h3 {
        width: 100%;
        font-style: italic;
        margin-bottom: 1rem;
    }
    img {
        height: 48px;
        width: 48px;
        border-radius: 50%;
        box-shadow: 0px 0px 6px 0px #00000038;
    }
    span {
        position: absolute;
        color: #353535;
        font-size: small;
        top: 1.2rem;
        right: 1rem;
    }
}
iframe {
    width: 100%;
    height: calc(560px / 1.618);
}
.description {
    line-height: 1.4;
    width: 560px;
    padding: 1rem;
    color: #353535;
    :first-child {
        font-weight: 600;
        color: black;
    }
}

/* SMARTPHONE DEVICES */
@media screen and (max-width: 992px) {
    .presentation {
        line-height: 1.77;
        width: 100%;
        flex-direction: column;
        h3 {
            text-align: center;
        }
        span {
            position: relative;
            top: 0;
            right: 0;
        }
    }
    .description {
        width: 100%;
    }
    iframe {
        width: 100vw;
        height: calc(100vw / 1.6);
    }
}
</style>